<template>
  <div id="step-1" class="animate__animated animate__backInUp" v-loading="loading" element-loading-text="正在等待 NFC 校验..." element-loading-background="rgba(243, 241, 235, 0.5)" element-loading-spinner="el-icon-loading">
    <br />
    <br />
    <br />
    <v-img fit="fill" style="width: 70vw; height: 70vw;margin:0 auto;" :src="NFT" class="animate__animated animate__bounceInDown"></v-img>
    <br />
    <br />
    <br />
    <br />
    <v-btn large block @click="nextStep()" style="color:#2158a2">
      点这里，再次将手机贴近卡片......
    </v-btn>
    <v-img :src="NFC" height="70" width="70" color="pink" style="float:right;top:-57px;right:-5px;"></v-img>
    <div class="NFC-Shake">
      <v-dialog v-model="dialog" hide-overlay transition="dialog-bottom-transition" fullscreen>
        <v-card style="background:rgba(0,0,0,0.6);display:flex;align-items:center;">
          <v-img :src="NFCShake" height="100%" width="100%" style="border-radius:50px;"></v-img>
        </v-card>
      </v-dialog>
    </div>
  </div>
</template>

<script>
import NFT from "../assets/images/NFT.png";
import NFC from "../assets/images/NFC.gif";
import NFCShake from "../assets/images/NFC-shake.gif";
export default {
  data: () => ({
    loading: false,
    NFT,
    NFC,
    NFCShake,
    dialog: false,
  }),
  mounted() {
    // location.reload();
  },
  methods: {
    refresh() {
      location.reload();
    },
    nextStep() {
      // this.loading = true;
      this.dialog = true;
      // setTimeout(() => {
      //   this.$router.push("/verify/step-2");
      //   this.loading = false;
      // }, 3000);
      window.ndef = new NDEFReader();
      window.ndef.scan().then(() => {
        window.ndef.onreadingerror = (event) => {
          alert("【温馨提示】：NFC 读取错误！");
          // this.loading = false;
          this.dialog = false;
        };
        window.ndef.onreading = (event) => {
          this.$router.push("/verify/step-2");
          // this.loading = false;
          this.dialog = false;
        };
      }).catch(error => {
        alert("【温馨提示】：NFC 启动错误！");
        // this.loading = false;
        this.dialog = false;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.chip.v-icon {
  align-items: center;
  justify-content: center;
  font-size: 80vw;
}
</style>
